.change-color-btn {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  color: #36395a;
  cursor: pointer;
  height: 48px;
  text-align: left;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  background-color: #fcfcfd;
  border-width: 0;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  font-family: JetBrains Mono, monospace;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
  list-style: none;
  transition: box-shadow .15s, transform .15s;
  display: inline-flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px #d6d6e7;
}

.change-color-btn:focus {
  box-shadow: inset 0 0 0 1.5px #d6d6e7, 0 2px 4px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px #d6d6e7;
}

.change-color-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px #d6d6e7;
}

.change-color-btn:active {
  transform: translateY(2px);
  box-shadow: inset 0 3px 7px #d6d6e7;
}

